import { AccountInfo, AuthenticationResult, PublicClientApplication, SilentRequest, InteractionRequiredAuthError, ServerError, BrowserAuthError } from "@azure/msal-browser";
import { msalConfig, b2cPolicies, apiConfig } from "./msalConfig";
import { UrlHelper } from "../src/common/urlHelper";
import { routes } from "../src/presentation/vue3/router/routes";

declare var includeProgram: () => void;
declare var getBasePath: () => string;

let homeAccountId: string | undefined = undefined;
let ssoInvitation: ISsoInvitationInfo | undefined;

function log(message: string, type: "log" | "warn" | "error") {
    console.log(`[${type}]`, message);
}

function signOut() {
    // request: https://github.com/AzureAD/microsoft-authentication-library-for-js/blob/dev/lib/msal-browser/docs/request-response-object.md#request
    msalObj.logoutRedirect({ postLogoutRedirectUri: msalConfig.auth.redirectUri });
}
function signIn(invitationId?: string) {
    log(`going 2 signin${invitationId ? " with invitation" : ""}`, "log");
    storeInvitationData(undefined, invitationId);
    msalObj.loginRedirect({ scopes: ["openid", ...apiConfig.b2cScopes], redirectUri: msalConfig.auth.redirectUri });
}
interface IInvitationData { email?: string, invitationId?: string };
function storeInvitationData(email?: string, invitationId?: string) {
    const invitationData: IInvitationData = { email, invitationId };
    sessionStorage.setItem("studio_inv", JSON.stringify(invitationData));
    window.history.pushState({}, "", getBasePath());
}
function clearInvitationData() {
    sessionStorage.removeItem("studio_inv");
}
function getAndClearInvitationData() {
    const item = sessionStorage.getItem("studio_inv");
    clearInvitationData();
    if (!item) { return undefined; }
    return (JSON.parse(item) as IInvitationData).invitationId;
}
async function invitedSignUp(email: string, invitationId: string) {
    log("going 2 invited signup", "log");
    storeInvitationData(email, invitationId);
    try {
        await msalObj.loginRedirect({
            scopes: ["openid", ...apiConfig.b2cScopes],
            authority: b2cPolicies.authorities.signUp.authority,
            extraQueryParameters: email ? { email } : undefined,
            redirectUri: msalConfig.auth.redirectUri,
        });
    } catch (err) {
        const berr = err as BrowserAuthError;
        if (berr.errorCode == "interaction_in_progress") {
            log("invited signup handling interaction_in_progress", "warn");
            const result = await handleRedirect();
            if (!result) { setTimeout(() => invitedSignUp(email, invitationId), 10); }
            return;
        }
        throw err;
    }
}

function setAccount(account: AccountInfo, redirected: boolean) {
    if (redirected) {
        const storedInvitationId = getAndClearInvitationData();
        if (storedInvitationId) { log("starting program with invitation data", "log"); ssoInvitation = { invitationId: storedInvitationId }; }
    }
    homeAccountId = account.homeAccountId;
    includeProgram();
}

function selectAccount() {
    const currentAccounts = msalObj.getAllAccounts(); //account retrieval https://github.com/AzureAD/microsoft-authentication-library-for-js/blob/dev/lib/msal-common/docs/Accounts.md
    if (currentAccounts.length < 1) { return false; }
    if (currentAccounts.length === 1) {
        return currentAccounts[0];
    }

    // SSO-TODO: understand this
    /**
     * Due to the way MSAL caches account objects, the auth response from initiating a user-flow
     * is cached as a new account, which results in more than one account in the cache. Here we make
     * sure we are selecting the account with homeAccountId that contains the sign-up/sign-in user-flow, 
     * as this is the default flow the user initially signed-in with.
     */
    const accounts = currentAccounts.filter(account =>
        account.homeAccountId.toUpperCase().includes(b2cPolicies.names.signUpSignIn.toUpperCase()) &&
        account.idTokenClaims!.iss!.toUpperCase().includes(b2cPolicies.authorityDomain.toUpperCase()) &&
        account.idTokenClaims!.aud === msalConfig.auth.clientId);

    if (accounts.length > 1) {
        // localAccountId identifies the entity for which the token asserts information.
        if (accounts.every(account => account.localAccountId === accounts[0].localAccountId)) {
            // All accounts belong to the same user
            return accounts[0];
        } else {
            // Multiple users detected. Logout all to be safe.
            signOut();
            return undefined;
        };
    } else if (accounts.length === 1) {
        return accounts[0];
    }
    return false;
}



async function handleRedirect() {
    let response: AuthenticationResult | null = null;
    try {
        response = await msalObj.handleRedirectPromise();
    } catch (err) {
        const serr = err as ServerError;
        if (serr instanceof ServerError) {
            if (serr.errorCode == "access_denied" && serr.message.indexOf("AADB2C90091") > -1) { console.warn("user did not consent"); return false; }
        }
        console.error(err);
    }
    if (!response) { return false; }
    /**
     * For the purpose of setting an active account for UI update, we want to consider only the auth response resulting
     * from SUSI flow. "tfp" claim in the id token tells us the policy (NOTE: legacy policies may use "acr" instead of "tfp").
     * To learn more about B2C tokens, visit https://docs.microsoft.com/en-us/azure/active-directory-b2c/tokens-overview
    */
    const tfp = (response.idTokenClaims as { tfp: string })['tfp'].toUpperCase();
    const names = [b2cPolicies.names.signUp, b2cPolicies.names.signUpSignIn].map(item => item.toUpperCase());
    if (names.indexOf(tfp) > -1) {
        // response object: https://github.com/AzureAD/microsoft-authentication-library-for-js/blob/dev/lib/msal-browser/docs/request-response-object.md#response
        const account = response.account || selectAccount();
        if (!account) { return; }

        setAccount(account, true);
    } else {
        log("redirect handled but name mismatch", "error");
    }
    return true;
}
async function handle<T>(func: () => Promise<T>) {
    try {
        return await func();
    } catch (error) {
        if (!(error instanceof InteractionRequiredAuthError)) { throw error; }
        return undefined;
    }
}
function createTokenRequest(accountId: string) {
    const tokenRequest: SilentRequest = {
        scopes: [...apiConfig.b2cScopes],
        forceRefresh: false, // Set this to "true" to skip a cached token and go to the server to get a new token
        account: msalObj.getAccountByHomeId(accountId)!, // account retrieval https://github.com/AzureAD/microsoft-authentication-library-for-js/blob/dev/lib/msal-common/docs/Accounts.md
    };
    return tokenRequest;
}
async function getTokenSilent(accountId: string) {
    const tokenRequest = createTokenRequest(accountId);
    const tokenResponse = await handle(() => msalObj.acquireTokenSilent(tokenRequest))
    if (tokenResponse?.accessToken) {
        log("silent token acquired at " + new Date().toString(), "log");
        return tokenResponse.accessToken;
    }

    const silentResponse = await handle(() => msalObj.ssoSilent(tokenRequest));
    if (silentResponse?.accessToken) {
        log("silent token acquired by ssoSilent at " + new Date().toString(), "log");
        return silentResponse.accessToken;
    }
    return undefined;
}
async function getToken() {
    if (!homeAccountId) { throw new Error("Unimplemented"); }

    const accessToken = await getTokenSilent(homeAccountId);
    if (accessToken) { return accessToken; }

    //SSO-TODO: prevent pop-up blocker
    // fallback to interaction when silent call fails
    const tokenRequest = createTokenRequest(homeAccountId);
    const popupResponse = await msalObj.acquireTokenPopup(tokenRequest);
    if (popupResponse.accessToken) {
        console.log("access_token acquired by popup at: " + new Date().toString());
        return popupResponse.accessToken;
    }
    throw new Error("Unimaginable");
}

function pathMatch(url: string, path: string): boolean {
    const urlParts = url.split("/").filter(part => part !== "");
    const pathParts = path.split("/").filter(part => part !== "");

    if (urlParts.length > pathParts.length) { return false; }
    for (let i = 0; i < pathParts.length; i++) {
        const pathPart = pathParts[i].toLowerCase();
        const urlPart = urlParts[i]?.toLowerCase();

        if (pathPart.endsWith("?")) {
            continue;
        } else if (pathPart.startsWith(":") && urlPart != undefined) {
            continue;
        } else if (pathPart !== urlPart) {
            return false;
        }
    }

    return true;
}
async function assertStarted(forPath: string): Promise<true> {
    if (homeAccountId != undefined) { return true; }
    await start(forPath);
    if (homeAccountId != undefined) { return true; }
    throw new Error("propably redirected");
}



const api: ISsoApi = {
    signOut,
    getToken,
    getInvitation: () => ssoInvitation,
    assertStarted,
};


function getInvitationMailData() {
    const { studio_email, studio_invitationid, studio_signup, } = new UrlHelper().gatherQueryString<{ studio_email?: string, studio_invitationid?: string, studio_signup?: string, }>();
    return { email: studio_email, invitationId: studio_invitationid, signUp: studio_signup, };
}
function handleUserCancel() {
    const fragment = new UrlHelper().gatherFragment<{ error?: string }>();
    if (fragment.error != "access_denied") { return false; }
    log("handling user cancel", "log");
    window.location.href = window.location.href.replace(window.location.hash, "");
    return true;
}


// Create the main myMSALObj instance
const msalObj = new PublicClientApplication(msalConfig);
async function start(forPath?: string) {
    const routeMath = routes.find(route => pathMatch(forPath || window.location.pathname, route.path));
    if (routeMath?.meta?.noAuth) { includeProgram(); return; }
    if (handleUserCancel()) { return; }
    const { email, invitationId, signUp, } = getInvitationMailData();
    if (signUp == "true" && email && invitationId) { invitedSignUp(email, invitationId); return; }

    const account = selectAccount();//page refresh
    const isRedirect = await handleRedirect();
    if (isRedirect) { log("handled redirect", "log"); return; }

    clearInvitationData();
    if (!account) { signIn(invitationId); return; }
    const token = await getTokenSilent(account.homeAccountId);
    if (!token) { signIn(invitationId); return; }
    setAccount(account, false);
}
start();

export { api };