export class UrlHelper {
    public gatherQueryString<T = Record<string, string | undefined>>() {
        const { search } = window.location;
        if (!search) { return {} as T; }

        return this.parseKeyValuePairString<T>(search.replace(/\?/g, "&"));
    }
    public gatherFragment<T = Record<string, string | undefined>>() {
        const { hash } = window.location;
        if (!hash) { return {} as T; }

        return this.parseKeyValuePairString<T>(hash.substring(1));
    }
    /**
     * @param data a string that has a key value pair string. pairs splitted with &. Key and value splitted by =. like x=5&y=7
     */
    private parseKeyValuePairString<T = Record<string, string | undefined>>(data: string) {
        return data.split("&")
            .map(item => { const dat = item.split("="); return { key: dat[0], value: dat[1] } })
            .reduce((prev, item) => { (prev as any)[item.key] = item.value; return prev; }, {} as T)
    }
}